.Skills {
  color: var(--white);
  background-color: var(--base-color);
}

.Skills section {
  margin-top: 24px;
}

.Skills h1 {
  color: var(--white);
}

.Skills ul {
  margin-top: 12px;
}

.Skills a {
  color: var(--white);
  text-decoration: none;
  transition: opacity 0.6s ease;
}

.Skills a:hover,
.Skills a:active {
  opacity: 0.6;
}
